.App{text-align:center}body{background-color:white}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.navbar{background:#000;height:80px;display:flex;justify-content:center;align-items:center;font-size:1.2rem;position:-webkit-sticky;position:sticky;top:0;z-index:999}.navbar-container{display:flex;justify-content:center;align-items:center;height:170px;background-image:linear-gradient(45deg, #0e0078 25%, #000 25%, #000 50%, #0e0078 50%, #0e0078 75%, #000 75%, #000 100%);background-size:14.14px 14.14px;border-bottom:3px solid #ff003c}.navbar-logo{color:#fff;justify-self:start;margin-left:20px;cursor:pointer;text-decoration:none;font-size:2rem;display:flex;align-items:center}.fa-typo3{margin-left:0.5rem;font-size:1.8rem}.nav-menu{display:grid;grid-template-columns:repeat(4, auto);grid-gap:10px;list-style:none;text-align:center;width:60vw;justify-content:end;margin-right:2rem}.nav-item{height:80px}.nav-links{color:#fff;display:flex;align-items:center;text-decoration:none;padding:0 1.3rem;height:100%;font-size:20px;font-family:"Archivo Narrow", sans-serif;letter-spacing:0.5px}.nav-links:hover{border-bottom:5px solid #fff;border-top:5px solid #fff;background-color:#080044;height:88%;transition:all 0.3s ease-out}.fa-bars{color:#fff}.nav-links-mobile{display:none}.menu-icon{display:none}@media screen and (max-width: 960px){.NavbarItems{position:relative}.nav-menu{display:flex;flex-direction:column;width:100%;height:90vh;position:absolute;top:80px;left:-100%;opacity:1;transition:all 0.5s ease}.nav-menu.active{background:#242222;left:0;opacity:1;transition:all 0.5s ease;z-index:1}.nav-links{text-align:center;padding:2rem;width:100%;display:table}.nav-links:hover{background-color:#fff;color:#242424;border-radius:0}.navbar-logo{position:absolute;margin-left:0}.menu-icon{display:block;position:absolute;top:0;right:0;-webkit-transform:translate(-100%, 60%);transform:translate(-100%, 60%);font-size:1.8rem;cursor:pointer}.fa-times{color:#fff;font-size:2rem}.nav-links-mobile{display:block;text-align:center;margin:2rem auto;border-radius:4px;width:80%;text-decoration:none;font-size:1.5rem;background-color:transparent;color:#fff;padding:14px 20px;border:1px solid #fff;transition:all 0.3s ease-out}.nav-links-mobile:hover{background:#fff;color:#242424;transition:250ms}}

:root{--primary: white}.btn{padding:8px 20px;border-radius:2px;outline:none;border:none;cursor:pointer}.btn--primary{background-color:white;background-color:var(--primary);color:black;border:1px solid white;border:1px solid var(--primary)}.btn--outline{background-color:transparent;color:white;padding:8px 20px;border:1px solid white;border:1px solid var(--primary);transition:all 0.3s ease-out}.btn--medium{padding:8px 20px;font-size:20px}.btn--medium:hover,.btn--large:hover{background:white;color:green;transition:all 0.3s ease-out}

.hero-container{height:100vh;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;box-shadow:inset 0 0 0 1000px rgba(0,0,0,0.2);object-fit:contain}.video{position:relative;width:100%;height:100%;object-fit:cover;z-index:-1}.mission-partone{font-size:46px;color:#fff;letter-spacing:3px;font-family:"Archivo Narrow", sans-serif;height:125px;text-align:center;position:absolute;text-transform:uppercase;animation:fadeIn 1s;-webkit-animation:fadeIn 1.5s;-moz-animation:fadeIn 1.5s}@keyframes fadeIn{0%{opacity:0}25%{opacity:1}100%{opacity:1}}@-webkit-keyframes fadeIn{0%{opacity:0}25%{opacity:1}100%{opacity:1}}.mission-parttwo{font-size:56px;color:#fff;letter-spacing:3px;font-family:"Archivo Narrow", sans-serif;line-height:-2px;text-align:center;height:0;position:absolute;text-transform:uppercase;animation:fadeIn 3.2s;-webkit-animation:fadeIn 3.2s;-moz-animation:fadeIn 3.2s}@keyframes fadeIn{0%{opacity:0}75%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeIn{0%{opacity:0}75%{opacity:0}100%{opacity:1}}.next-page{display:flex;justify-content:center}.swiper{font-size:22px;color:#fff;letter-spacing:2px;font-family:"Archivo Narrow", sans-serif;text-align:center;position:absolute;bottom:-40px;text-transform:uppercase;animation:fadeIn 5.5s;-webkit-animation:fadeIn 5.5s;-moz-animation:fadeIn 5.5s}@keyframes fadeIn{0%{opacity:0}80%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeIn{0%{opacity:0}80%{opacity:0}100%{opacity:1}}.round{position:absolute;border:2px solid #fff;width:40px;height:40px;border-radius:100%;bottom:-110px;animation:fadeIn 7s;-webkit-animation:fadeIn 7s;-moz-animation:fadeIn 7s;-webkit-transform:rotate(90deg);transform:rotate(90deg)}@keyframes fadeIn{0%{opacity:0}80%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeIn{0%{opacity:0}80%{opacity:0}100%{opacity:1}}#cta{width:100%;cursor:pointer;position:absolute}#cta .arrow{left:30%}.arrow{position:absolute;bottom:0;margin-left:0px;width:12px;height:12px;background-size:contain;top:15px;-webkit-animation-name:bounceAlpha;animation-name:bounceAlpha;-webkit-animation-duration:1.4s;animation-duration:1.4s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear}.segunda{margin-left:8px}.next{background-image:url(data:image/svg+xml;base64,)}@-webkit-keyframes bounceAlpha{0%{opacity:1;-webkit-transform:translateX(0px) scale(1);transform:translateX(0px) scale(1)}25%{opacity:0;-webkit-transform:translateX(10px) scale(0.9);transform:translateX(10px) scale(0.9)}26%{opacity:0;-webkit-transform:translateX(-10px) scale(0.9);transform:translateX(-10px) scale(0.9)}55%{opacity:1;-webkit-transform:translateX(0px) scale(1);transform:translateX(0px) scale(1)}}@keyframes bounceAlpha{0%{opacity:1;-webkit-transform:translateX(0px) scale(1);transform:translateX(0px) scale(1)}25%{opacity:0;-webkit-transform:translateX(10px) scale(0.9);transform:translateX(10px) scale(0.9)}26%{opacity:0;-webkit-transform:translateX(-10px) scale(0.9);transform:translateX(-10px) scale(0.9)}55%{opacity:1;-webkit-transform:translateX(0px) scale(1);transform:translateX(0px) scale(1)}}.bounceAlpha{-webkit-animation-name:bounceAlpha;animation-name:bounceAlpha;-webkit-animation-duration:1.4s;animation-duration:1.4s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear}.arrow.primera.bounceAlpha{-webkit-animation-name:bounceAlpha;animation-name:bounceAlpha;-webkit-animation-duration:1.4s;animation-duration:1.4s;-webkit-animation-delay:0.2s;animation-delay:0.2s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear}

