late(-2px, -3px)}50%{-webkit-transform:translate(1px, 3px);transform:translate(1px, 3px)}55%{-webkit-transform:translate(-3px, 2px);transform:translate(-3px, 2px)}69%{-webkit-transform:translate(1px, 1px);transform:translate(1px, 1px)}75%{-webkit-transform:translate(-2px, -1px);transform:translate(-2px, -1px)}91%{-webkit-transform:translate(1px, 3px);transform:translate(1px, 3px)}95%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(-2px, 0);transform:translate(-2px, 0)}}.strength-title{font-size:30px;color:#fff;letter-spacing:2px;font-family:"Archivo Narrow", sans-serif;text-align:center;text-transform:uppercase}

